import React, { Component } from 'react'
import logo from '../Resource/angular_project_logo2-removebg-preview.png'
import '../css/Style.css'
export class Navv extends Component {
    render() {
        return (
            <div>
                <nav className="navbar navbar-dark bg-dark navbar-expand-lg bgnav">
                    <div className="container-fluid">
                        <a className="navbar-brand" href="/">
                            <img src={logo} alt="Bootstrap" width="100px" height="80px" />
                        </a>
                        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarScroll" aria-controls="navbarScroll" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarScroll">
                            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                                <li className="nav-item">
                                    <a className="nav-link active text text-light" aria-current="page" href="/">Home</a>
                                </li>

                                <li className="nav-item">
                                    <a className="nav-link text text-light" href='/products'>Our Products</a>
                                </li>

                                <li className="nav-item">
                                    <a className="nav-link text text-light" href='/contact'>Contact Us</a>
                                </li>

                                <li className="nav-item">
                                    <a className="nav-link text text-light" href="/admin">Admin</a>
                                </li>
                            </ul>

                            <ul className="navbar-nav m-2">

                            <li className="nav-item">
                                    <a className="nav-link text text-light" href="/login"><i className="fa fa-user-circle" aria-hidden="true"></i> login </a>
                                </li>

                                <li className="nav-item">
                                    <a className="nav-link text text-light" href="/signup"><i className="fa fa-user-plus" aria-hidden="true"></i> Signup </a>
                                </li>

                                <li>
                                    <a className="nav-link text text-light" href='/cart'><i className="fa fa-shopping-cart" aria-hidden="true"></i> cart</a>
                                </li>

                            </ul>

                        </div>
                    </div>
                </nav >

            </div >
        )
    }
}

export default Navv